#join{
    width: 100%; padding-bottom: 20px;
    background-color: rgba(243,242,237,1);

    .topPic {
        background: url(/public/img/slider7.jpg) bottom; width: 100%;
        height: 450px; background-repeat: no-repeat; background-attachment: fixed;
        background-size: cover; position: relative;

        p {
            position: absolute; font: bold 60px/1.8 "DM Serif Display"; letter-spacing: 3px;
            color: #fff; top: 26%; left: 50%; transform: translateX(-50%);
            
        }
        span {
            position: absolute; font: normal 25px "Cardo";
            color: #FFF;top: 45%; left: 25%;
        }
    
        aside{
            width: 800px; height: 100px; position: absolute; top: 40%; left: 50%; transform: translateX(-50%);
            overflow: hidden; z-index: 3;
    
            .top {
                width: 37%;  height: 1px; background: #fff;
                position: absolute; top: 0; right: 0px;
            }
            .right {
                width: 1px; height: 100%; background: #fff;
                position: absolute; top: 0; right: 0;
            }
            .bottom {
                width: 100%; height: 1px; background: #fff;
                position: absolute; bottom: 0; right: 0;
            }
            .left {
                width: 1px; height: 100%; background: #fff;
                position: absolute; bottom: 0; left: 0;
            }
            .topLeft {
                width: 37%;
                height: 1px; background: #fff; position: absolute; top: 0; left: 0;
            }
        }
      }

    .inner {
        width: 1200px; margin: 20px auto; position: relative; padding: 100px 0 0;
        overflow: hidden;
        .pic {
            position: absolute; left: 603px; top: 230px;
            width: 1612px; height: 668px; opacity: 0.05;
            background-image: url(/public/img/began_bg.png);
            background-size: cover; background-repeat: no-repeat;
        }
        h2 {
            width: 311px; letter-spacing: 1px;
            margin-top: 50px; color: rgba(88,91,73,1);
            font: bold 40px/1.5 "Noto Serif Display"; text-align: left;
        }
        .p1{
            margin: 20px 0;
            width: 40%; font: normal 16px/1.8 "Baloo 2"; 
            font-weight: 300; text-align: left; letter-spacing: 0.5px;
        }
        .icons {
            
            .message
            ,.phone {
                margin-top: 30px; color: #000; font: normal 14px "Baloo 2";
                font-weight: 300; display: block; margin-bottom: 20px;
                i{
                    font-size: 23px; transform: translateY(5px);
                    color: rgba(88,91,73,1); margin-right: 20px;
                }
            }
            
            >span {
                display: block; margin-bottom: 20px;
                width: 303px; font: normal 14px/1.2 "Baloo"; letter-spacing: 1px;
                color: rgba(179,163,149,1); text-align: left;
            }
        }
        
        .p2 {
            transform: translateY(20px); margin: 20px 0;
            width: 40%; font: normal 14px/1.8 "Baloo 2";
            text-align: left; letter-spacing: 0.8px;
        }
        form {
            width: 50%; margin-left: auto; margin-top: -438px; position: relative;
            border-radius: 0;
            table {
                width: 100%;
                th {
                    text-indent: -9999px; opacity: 0;

                    &:last-child {
                        text-indent: 0; opacity: 1;
                    }
                    input[type=reset], input[type=submit] {
                        height: 41px; width: 140px; background-color: rgba(171,142,115,1);
                        border-color: rgba(171,142,115,1); color: #fff; cursor: pointer;
                        font: normal 13px/39px "Baloo 2"; font-weight: 300; 
                        letter-spacing: 1.2px; text-align: center; margin: 35px 25px;
                        transition: 0.5s; transform: translateX(-15px);

                        &:hover {
                            background-color: rgba(179,163,149,1);
                            border-color: rgba(179,163,149,1);
                        }
                    }
                }
                td {
                    
                    input[type=text], input[type=password], input[type=email] {
                        width: 90%; height: 41px; background-color: #fff; border: none; outline: none;
                        font: normal 13px/41px "Baloo 2"; padding: 10px; color: #000; font-weight: 300;
                        margin-bottom: 15px;
                    }
                    input[type=checkbox], input[type=radio] {
                        margin-right: 55px; 
                    }
                    label {
                        font: normal 14px/30px "Baloo 2"; margin-right: 7px;
                        margin-left: 10px;
                    }
                    select {
                        width: 90%; font: normal 13px/21px "Baloo 2"; padding: 10px; color: #000; font-weight: 300;
                         outline: none; border: none; margin-bottom: 15px;
                    }
                    textarea {
                        height: 194px;  width: 90%;  border: none; outline: none; margin-bottom: 15px;
                        font: normal 13px/21px "Baloo 2"; padding: 10px;  font-weight: 300;
                    }

                    .err {
                        display: block; width: 90%; text-align: left;
                        font: 11px/1 "Baloo 2"; color: red;margin-left: 10px;transform: translateY(-7px);
                    }
                }
            }
            
        }
        .pop {
            width: 500px; height: 250px; margin: 200px auto; box-shadow: 10px 10px 10px #d6cdaa;
            z-index: 10; background-color: #e9e4d0; position: absolute; top: 20%; left: 50%; transform: translateX(-50%);
            padding: 50px;
            h1 {
                width: 100%; text-align: center; font: bold 25px/1.8 "Sans Serif";
                 color: #504037;
            } 
            .clapPic {  
                margin-top: 13px; text-align: center;
                font-size: 32px; color: rgba(177,136,87,.28);
            } 
            .close {
                outline: none; border: none; color: #504037; background-color: inherit;
                 cursor: pointer; display: block; margin: 30px auto 0; 
                font: normal 15px/1 "Baloo 2"; transition: 0.5s; letter-spacing: 1px;
    
                &:hover { color: #b18857;}
            } 
        }
    }
}

@media screen and (max-width : 1500px) {
    #join {
      .topPic {
        span {
          left: 21%;
        }
      }
    }
  }

@media screen and (max-width: 1179px){
    #join{
        width: 100%; 
    
        .topPic {
            p {
                width: 100%; position: relative; margin: 0 auto; text-align: center; 
                color: #fff; top: 36%; left: 50%; transform: translate(-50%,-50%);
            }
            span {
                display: block; width: 50%; margin: 0 auto; text-align: center; 
                top: 45%; left: 50%; transform: translateX(-50%);
            }
            aside{ display: none; }
        }
    
        .inner {
            width: 100%; margin: 0px auto; position: relative; padding: 150px 0 50px; overflow: hidden;
    
            .pic {
                position: absolute; left: 403px; top: 230px;
                width: 1612px; height: 668px; opacity: 0.05;
                background-image: url(/public/img/began_bg.png);
                background-size: cover; background-repeat: no-repeat;
            }
            h2 {
                width: 90%; margin: 0 auto;
                position: relative; top: auto; left: auto; text-align: center;
            }
            .p1{
                position: relative; top: auto; left: auto; margin-top: 0px;
                width: 70%; margin: 20px auto; text-align: center;
            }
            .icons{
                width: 73%; margin: 0 auto; display: flex; justify-content: space-between; padding-left: 20px;
                .message
                ,.phone {
                    margin-top: 20px; 
                }
                .message{position: relative; top: auto; left: auto; margin-right: 0px;}
                .phone {position: relative; top: auto; left: auto; }
                >span {
                    position: relative; top: auto; left: auto;
                    width: 303px; text-align: left; display: block; margin-top: 26px;
                }
            }
            
            .p2 {
                position: relative; top: auto; left: auto;
                width: 70%; margin: 20px auto;
            }
            form {
                width: 80%; position: relative; right: auto; top: auto; left: auto;
                margin: 60px auto 0; text-align: center;
            }
            
        }
        .pop { margin: -600px auto;}
    }
}

@media screen and (max-width : 960px) {
    #join{
        width: 100%; padding-bottom: 10px;
    
        .inner {
            width: 450px; margin: 40px auto 0; position: relative; padding: 150px 0 0;
            form {
                table {
                    td {
                        input[type=checkbox] {
                            margin-right: 44px;
                        }
                    }
                }
            }
    
            .pic {
                position: absolute; left: 603px; top: 230px;
                width: 1612px; height: 668px; opacity: 0.05;
                background-image: url(/public/img/began_bg.png);
                background-size: cover; background-repeat: no-repeat;
            }
            h2 {
                font: bold 30px/1.5 "Noto Serif Display";
            }
            .icons{
                display: block;
                .phone {display: block;}
                
            }
        }
        .pop {
            width: 300px;
            h1 {
                font: bold 20px/1.8 "Sans Serif";
            }
        }
    }
}




